
import { Component, Provide } from 'vue-property-decorator'
import Vue from 'vue'
import { Store } from '@/core/store/store'
import { store } from '@/main'
import { MauritanianActor } from '@/core/dataDescription/mauritanianActor'
import { InternationalActor } from '@/core/dataDescription/internationalActor'
import { getPopUpClass } from '@/utils/mapUtils'
const devEnv = process.env.NODE_ENV === 'development'
const oddURL = devEnv ? '/img/thematics/' : `${process.env.BASE_URL}img/thematics/`

@Component
export default class ActorDescription extends Vue {
  @Provide('store') private store: Store = store
  getPopUpClass = getPopUpClass
  window = window
  mapURL = devEnv ? '/img/maps/' : `${process.env.BASE_URL}img/maps/`

  get actor (): MauritanianActor | InternationalActor {
    return store.state._actors.filter(x => x.appId === store.state._selectedActor).pop() as MauritanianActor | InternationalActor
  }

  getMapImg (string: string) :string {
    return this.mapURL + string
  }

  getOddUrl (code: string): string {
    return store.state._odd.filter(x => x.code === code).pop()?.url as string
  }

  getOddIcon (code: string): string {
    return oddURL + store.state._odd.filter(x => x.code === code).pop()?.icon as string
  }
}
