/* eslint-disable @typescript-eslint/no-explicit-any */

import axios from 'axios'
import { State } from './state'
import Papa from 'papaparse'
import { Odd } from '../dataDescription/odd'
import { MauritanianActor, RawMauritanianActor } from '../dataDescription/mauritanianActor'
import { InternationalActor, RawInternationalActor } from '../dataDescription/internationalActor'
import { extractCoords } from '@/utils/mapUtils'

export class Store {
  state!: State
  _rawCountriesList: string[] = []
  _rawAssociationTypes: string[] = []

  async getData (): Promise<string> {
    try {
      const rawNationalsActors = await axios.get('https://docs.google.com/spreadsheets/d/e/2PACX-1vStb-7Y1KwdK41kSv4D7dqk9DXJehAVqNm02fHTXFwIii2sdL7rVZm3pTDk2KeFZHUkfLM2MEXXofV5/pub?gid=1026036749&single=true&output=csv')
      const parsedNationalsActors = Papa.parse(rawNationalsActors.data, {
        header: true
      })
      const proceceedNationalActors = this.proceedMauritianActors(parsedNationalsActors.data as RawMauritanianActor[])
      const rawInternationalsActors = await axios.get('https://docs.google.com/spreadsheets/d/e/2PACX-1vStb-7Y1KwdK41kSv4D7dqk9DXJehAVqNm02fHTXFwIii2sdL7rVZm3pTDk2KeFZHUkfLM2MEXXofV5/pub?gid=274648396&single=true&output=csv')
      const parsedInternationalsActors = Papa.parse(rawInternationalsActors.data, {
        header: true
      })
      const proceceedInternationalActors = this.proceedInternationalActors(parsedInternationalsActors.data as RawInternationalActor[])
      const rawOdd = await axios.get('https://docs.google.com/spreadsheets/d/e/2PACX-1vStb-7Y1KwdK41kSv4D7dqk9DXJehAVqNm02fHTXFwIii2sdL7rVZm3pTDk2KeFZHUkfLM2MEXXofV5/pub?gid=943701079&single=true&output=csv')
      const parsedOdd = Papa.parse(rawOdd.data, {
        header: true
      })
      // console.log(proceceedNationalActors)
      console.log(proceceedInternationalActors)
      // console.log(parsedOdd.data)
      const actors:Array<MauritanianActor | InternationalActor> = [...proceceedNationalActors, ...proceceedInternationalActors]
      this.state = new State((parsedOdd.data as Odd[]), actors, [...new Set(this._rawAssociationTypes)], [...new Set(this._rawCountriesList)])
      return Promise.resolve('Data recovery achieved')
    } catch (error) {
      console.log(error)
      // eslint-disable-next-line prefer-promise-reject-errors
      return Promise.reject('An error occured during data recovery')
    }
  }

  proceedMauritianActors (rawData: RawMauritanianActor[]): MauritanianActor[] {
    const proceceed: MauritanianActor[] = []
    rawData.map((x, i) => {
      const proceceedData = { ...x };
      (proceceedData as unknown as MauritanianActor).gps = extractCoords(x)
      if ((proceceedData as unknown as MauritanianActor).gps.length === 0) {
        (proceceedData as unknown as MauritanianActor).validLocation = false
      }
      (proceceedData as any).localisation = 'Nationale';
      (proceceedData as any).odd1 = x.lien_odd1;
      (proceceedData as any).odd2 = x.lien_odd2;
      (proceceedData as any).appId = i
      this._rawCountriesList.push(proceceedData.pays_origine)
      this._rawAssociationTypes.push(proceceedData.type)
      proceceed.push(proceceedData as unknown as MauritanianActor)
    })
    return proceceed
  }

  proceedInternationalActors (rawData: RawInternationalActor[]): InternationalActor[] {
    const proceceed: InternationalActor[] = []
    rawData.map((x, i) => {
      const proceceedData = { ...x };
      (proceceedData as unknown as InternationalActor).gps = extractCoords(x)
      if ((proceceedData as unknown as InternationalActor).gps.length === 0) {
        (proceceedData as unknown as InternationalActor).validLocation = false
      }
      (proceceedData as any).localisation = 'Internationale';
      (proceceedData as any).odd1 = x.odd1;
      (proceceedData as any).odd2 = x.odd2;
      (proceceedData as any).appId = i
      this._rawCountriesList.push(proceceedData.pays_origine)
      this._rawAssociationTypes.push(proceceedData.type)
      proceceed.push(proceceedData as unknown as InternationalActor)
    })
    return proceceed
  }
}
