import { InternationalActor, RawInternationalActor } from '@/core/dataDescription/internationalActor'
import { MauritanianActor, RawMauritanianActor } from '@/core/dataDescription/mauritanianActor'
import { FeatureCollection, Geometry, GeoJsonProperties } from 'geojson'

export const datasetToGeoJSON = (dataset: Array<MauritanianActor | InternationalActor>): FeatureCollection => {
  const geojson: FeatureCollection<Geometry, GeoJsonProperties> = {
    type: 'FeatureCollection',
    features: []
  }
  dataset.map(x => {
    if (x.gps && x.gps.length > 0) {
      x.gps.map((p) => {
        geojson.features.push({
          type: 'Feature',
          properties: { ...x },
          geometry: {
            type: 'Point',
            coordinates: [
              p[1],
              p[0]
            ]
          }
        })
      })
    }
  })
  return geojson
}

export const extractCoords = (actor: RawMauritanianActor | RawInternationalActor):[number, number][] => {
  const places = ['nouakchott', 'guidimakha', 'gorgol', 'hodh_el_harbi', 'hodh_ech_chargui', 'assaba', 'brakna', 'tagant', 'trarza', 'tiris_zemmour', 'adrar', 'dakhlet_nouadhibou', 'Inchiri', 'GPS_siege_europe']
  const validCoords: [number, number][] = []
  places.map(p => {
    const coord = (actor as any)[p] ? (actor as any)[p].split(',') : null
    if (coord) {
      const confirmedCoord: number[] = []
      coord.map((c: string) => {
        if (!isNaN(Number(c))) confirmedCoord.push(Number(c))
      })
      if (confirmedCoord.length === 2) validCoords.push(confirmedCoord as [number, number])
    }
  })
  return validCoords
}

export const getPopUpClass = (AssociationType: string): string => {
  switch (AssociationType) {
    case 'Agence / Organisme public':
      return '#D1584C'
      break
    case 'Association':
      return '#7BAF7A'
      break
    case 'Collectivit√© territoriale':
      return '#FFBE06'
      break
    case 'Culturel':
      return '#cccccc'
      break
    case 'Entreprise / Syndicat':
      return '#465423'
      break
    case 'ONG internationale':
      return '#FF9E2A'
      break
    case 'OSIM (diaspora)':
      return '#F76564'
      break
    case 'Recherche / Apprentissage':
      return '#A0C9F1'
      break
    default:
      return '#b3b3b3'
      break
  }
}
