
import { Component, Provide, Watch } from 'vue-property-decorator'
import Vue from 'vue'
import { Store } from '@/core/store/store'
import { store } from '@/main'
import maplibregl, { GeoJSONSource, GeoJSONSourceSpecification, LngLat, LngLatBounds, Map, Popup, StyleSpecification } from 'maplibre-gl'
import { mapStyle } from '@/core/map/mapStyle'
import 'maplibre-gl/dist/maplibre-gl.css'
import { getPopUpClass, datasetToGeoJSON } from '@/utils/mapUtils'
import { grdrClusters, grdrClustersCount, grdrMarkers } from '@/core/map/grdrMarkers'
import { mask } from '@/userside/assets/map/mask'
import { communes } from '@/userside/assets/map/communes'
import { regions } from '@/userside/assets/map/regions'
import { maskStyle } from '@/core/map/mask'
import { PopUpContent } from '@/core/map/popup'
import { regionsBoundaries, regionsLabels } from '@/core/map/regions'
import { communesBoundaries, communesLabels } from '@/core/map/communes'
import { selectedGrdrMarkers } from '@/core/map/selectedGrdrMarkers'
import ActorDescription from './ActorDescription.vue'

@Component({
  components: { ActorDescription }
})
export default class MapComponent extends Vue {
  @Provide('store') private store: Store = store
    _map!: Map
    _bbox!: LngLatBounds
    _popUp: Popup = new maplibregl.Popup({ maxWidth: '500px' })

    get showLeftMenu (): boolean {
      return store.state.showLeftMenu
    }

    set showLeftMenu (value) {
      setTimeout(() => this._map.resize(), 100)
      store.state.showLeftMenu = value
    }

    @Watch('store.state._filteredActors')
    @Watch('store.state._selectedActor')
    refreshActorsOnMap (): void {
      const source:GeoJSONSource = this._map.getSource('grdrSource') as GeoJSONSource
      if (source) {
        if (store.state._selectedActor) {
          source.setData(datasetToGeoJSON(store.state._filteredActors.filter(x => x.appId !== store.state._selectedActor)))
        } else {
          source.setData(datasetToGeoJSON(store.state._filteredActors))
        }
      }
      const source2:GeoJSONSource = this._map.getSource('selectedGrdrSource') as GeoJSONSource
      if (source2) {
        if (store.state._selectedActor) {
          source2.setData(datasetToGeoJSON(store.state._filteredActors.filter(x => x.appId === store.state._selectedActor)))
        } else {
          source2.setData(datasetToGeoJSON([]))
        }
      }
    }

    @Watch('store.state._selectedMapContext')
    changeMapContext (): void {
      if (store.state._selectedMapContext === 'MIR') {
        this._bbox = store.state._MIRbbox
      } else {
        this._bbox = store.state._UEbbox
      }
      this.resetZoom()
    }

    get selectedActor (): number | null {
      return store.state._selectedActor
    }

    unselectActor () : void {
      store.state._selectedActor = null
    }

    mounted (): void {
      this._bbox = store.state._MIRbbox
      this._map = new maplibregl.Map({
        container: 'map',
        style: mapStyle as StyleSpecification,
        center: [-11.491699, 20.869078],
        zoom: 5.5
      })
        .addControl(new maplibregl.AttributionControl({
          customAttribution: '&copy; <a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a>, &copy; <a href="https://carto.com/attributions">CARTO</a>'
        }), 'bottom-left')

      this._map.on('load', () => {
        this.setSources()
      })
    }

    setSources (): void {
      const maskSource: GeoJSONSourceSpecification = {
        type: 'geojson',
        data: mask
      }
      this._map.addSource('maskSource', maskSource)
      const regionsSource: GeoJSONSourceSpecification = {
        type: 'geojson',
        data: regions
      }
      this._map.addSource('regionsSource', regionsSource)
      const communesSource: GeoJSONSourceSpecification = {
        type: 'geojson',
        data: communes
      }
      this._map.addSource('communesSource', communesSource)
      const grdrSource: GeoJSONSourceSpecification = {
        type: 'geojson',
        data: datasetToGeoJSON(store.state._filteredActors),
        cluster: true,
        clusterRadius: 30
      }
      this._map.addSource('grdrSource', grdrSource)
      const selectedGrdrSource: GeoJSONSourceSpecification = {
        type: 'geojson',
        data: datasetToGeoJSON([])
      }
      this._map.addSource('selectedGrdrSource', selectedGrdrSource)

      this.setLayers()
    }

    setLayers (): void {
      this._map.addLayer(maskStyle)
      this._map.addLayer(regionsBoundaries)
      this._map.addLayer(regionsLabels)
      this._map.addLayer(communesBoundaries)
      this._map.addLayer(communesLabels)
      this._map.addLayer(grdrClusters)
      this._map.addLayer(grdrClustersCount)
      this._map.addLayer(grdrMarkers)
      this._map.addLayer(selectedGrdrMarkers)

      this._map.on('mouseenter', 'clusters', this.setMapCursorPointer.bind(this))
      this._map.on('mouseleave', 'clusters', this.removeMapCursorPointer.bind(this))

      this._map.on('click', 'clusters', (e) => {
        const features = this._map.queryRenderedFeatures(e.point, {
          layers: ['clusters']
        })
        const clusterId = features[0].properties.cluster_id
        const clusterSource = this._map.getSource('grdrSource') as GeoJSONSource
        clusterSource.getClusterExpansionZoom(
          clusterId,
          (err, zoom) => {
            if (err) return
            this._map.easeTo({
              center: (features[0] as any).geometry.coordinates,
              zoom: zoom as number
            })
          }
        )
      })

      this._map.on('mouseenter', 'grdrMarkers', (e) => {
        const popup: PopUpContent[] = [{
          appId: e.features![0].properties!.appId,
          nom: e.features![0].properties!.nom,
          type: e.features![0].properties!.type
        }]
        this.setMapPopUp(popup, e.lngLat)
      })

      this._map.on('click', 'grdrMarkers', (e) => {
        store.state.setSelectedActor(e.features![0].properties!.appId)
      })
      this._map.on('mouseleave', 'grdrMarkers', this.removePopUp.bind(this))
      this._map.on('mouseenter', 'grdrMarkers', this.setMapCursorPointer.bind(this))
      this._map.on('mouseleave', 'grdrMarkers', this.removeMapCursorPointer.bind(this))
    }

    setMapPopUp (content: PopUpContent[], lnglat: LngLat): void {
      let HTMLString = '<div class="ml-2 mr-2 pop-up-container">'

      content.map((x) => {
        HTMLString += `<div class="popup-title popup-pointer" id="${x.appId}">${this.cropString(x.nom)}</div>`
        HTMLString += `<div class="popup-asso popup-pointer" style="background-color:${getPopUpClass(x.type)}"id="${x.appId}">${x.type}</div>`
      })

      HTMLString += '</div>'
      this._popUp = new maplibregl.Popup({
        maxWidth: '500px',
        closeButton: false
      })
        .setLngLat(lnglat)
        .setHTML(HTMLString)
      this._popUp.addTo(this._map)
      this._popUp.getElement().addEventListener('click', (e) => {
        if ((e.target as any).id) {
          store.state.setSelectedActor(Number((e.target as any).id))
        }
        this.removePopUp()
      })
    }

    setMapCursorPointer (): void {
      this._map.getCanvas().style.cursor = 'pointer'
    }

    removeMapCursorPointer (): void {
      this._map.getCanvas().style.cursor = ''
    }

    removePopUp (): void {
      if (this._popUp) this._popUp.remove()
    }

    zoomMore (): void {
      this._map.zoomIn()
    }

    zoomLess (): void {
      this._map.zoomOut()
    }

    resetZoom (): void {
      this._map.fitBounds(this._bbox)
    }

    cropString (string: string): string {
      return string.length > 40 ? string.substring(0, 40) + '...' : string
    }
}
