import { LngLatBounds } from 'maplibre-gl'
import { InternationalActor } from '../dataDescription/internationalActor'
import { MauritanianActor } from '../dataDescription/mauritanianActor'
import { Odd } from '../dataDescription/odd'
import { regionsList } from '../dataDescription/regions'

export class State {
  showLeftMenu = true
    _selectedMapContext: 'MIR' | 'UE' = 'MIR'
    _MIRbbox: LngLatBounds = new LngLatBounds([-18.764648, 14.455958], [-4.218750, 27.059126])
    _UEbbox: LngLatBounds = new LngLatBounds([-14.062500, 35.889050], [19.687500, 58.077876])
    _selectedActor: null | number = null
    _actors!: Array<MauritanianActor | InternationalActor>
    _filteredActors!:Array<MauritanianActor | InternationalActor>
    _odd!: Odd[]
    _mauritianActors!: MauritanianActor[]
    _internationalActors!: InternationalActor[]
    _countries!: string[]
    _actorsTypes!: string[]
    _regions = regionsList
    _filters!: {
      selectedActors: string[],
      selectedCountries: string[],
      selectedTypes: string[],
      selectedOdds: string[]
      selectedRegions: string[]
    }

    constructor (odd: Odd[], actors:Array<MauritanianActor | InternationalActor>, actorsTypes: string[], countriesList: string[]) {
      // eslint-disable-next-line no-return-assign
      actors.map((x, i) => x.appId = i + 1)
      this._odd = odd
      this._actors = actors
      this._filteredActors = actors
      this._actorsTypes = actorsTypes
      this._countries = countriesList

      this._filters = {
        selectedActors: [],
        selectedCountries: [],
        selectedTypes: [],
        selectedOdds: [],
        selectedRegions: []
      }
    }

    setSelectedMapContext (context: 'MIR' | 'UE'): void {
      this._selectedMapContext = context
    }

    setSelectedActor (id: number | null): void {
      this._selectedActor = id
    }

    filterActors (): void {
      this._filteredActors = this._filters.selectedActors.length > 0 ? this._actors.filter(x => this._filters.selectedActors.includes(x.nom)) : this._actors
      if (this._filters.selectedCountries.length > 0) {
        this._filteredActors = this._filteredActors.filter(x => this._filters.selectedCountries.includes(x.pays_origine))
      }
      if (this._filters.selectedTypes.length > 0) {
        this._filteredActors = this._filteredActors.filter(x => this._filters.selectedTypes.includes(x.type))
      }
      if (this._filters.selectedOdds.length > 0) {
        this._filteredActors = this._filteredActors.filter(x => {
          const odds = [x.odd1, x.odd2]
          return odds.some(o => this._filters.selectedOdds.includes(o))
        })
      }
      if (this._filters.selectedRegions.length > 0) {
        this._filteredActors = this._filteredActors.filter(x => this._filters.selectedRegions.some(r => (x as any)[r]))
      }
    }

    setSelectedActors (selected: string[]): void {
      this._filters.selectedActors = selected
      this.filterActors()
    }

    setSelectedCountries (selected: string[]): void {
      this._filters.selectedCountries = selected
      this.filterActors()
    }

    setSelectedTypes (selected: string): void {
      if (this._filters.selectedTypes.includes(selected)) {
        this._filters.selectedTypes = this._filters.selectedTypes.filter(x => x !== selected)
      } else {
        this._filters.selectedTypes.push(selected)
      }
      console.log(this._filters.selectedTypes)
      this.filterActors()
    }

    setSelectedOdds (selected: string[]): void {
      this._filters.selectedOdds = selected
      this.filterActors()
    }

    setSelectedRegions (selected: string[]): void {
      this._filters.selectedRegions = selected
      this.filterActors()
    }
}
