
import { Component, Provide } from 'vue-property-decorator'
import Vue from 'vue'
import { Store } from '@/core/store/store'
import { store } from '@/main'
import { InternationalActor } from '@/core/dataDescription/internationalActor'
import { MauritanianActor } from '@/core/dataDescription/mauritanianActor'
import { Odd } from '@/core/dataDescription/odd'
import { Region } from '@/core/dataDescription/regions'
import { getPopUpClass } from '@/utils/mapUtils'

@Component
export default class LeftMenu extends Vue {
  @Provide('store') private store: Store = store

  getPopUpClass = getPopUpClass

  get selectedMapContext () :'MIR' | 'UE' {
    return store.state._selectedMapContext
  }

  set selectedMapContext (context: 'MIR' | 'UE') {
    store.state.setSelectedMapContext(context)
  }

  get filteredActors () :Array<MauritanianActor | InternationalActor> {
    return store.state._filteredActors
  }

  get actors ():(MauritanianActor | InternationalActor)[] {
    return store.state._actors.sort((a, b) => (a.nom.localeCompare(b.nom)))
  }

  get selectedActors ():string[] {
    return store.state._filters.selectedActors
  }

  set selectedActors (selected:string[]) {
    store.state.setSelectedActors(selected)
  }

  get countries (): string[] {
    return store.state._countries.sort()
  }

  get selectedCountries ():string[] {
    return store.state._filters.selectedCountries
  }

  set selectedCountries (selected:string[]) {
    store.state.setSelectedCountries(selected)
  }

  get odds (): Odd[] {
    return store.state._odd
  }

  get selectedOdds ():string[] {
    return store.state._filters.selectedOdds
  }

  set selectedOdds (selected:string[]) {
    store.state.setSelectedOdds(selected)
  }

  get regions (): Region[] {
    return store.state._regions.sort()
  }

  get selectedRegions ():string[] {
    return store.state._filters.selectedRegions
  }

  set selectedRegions (selected:string[]) {
    store.state.setSelectedRegions(selected)
  }

  get actorsTypes (): string[] {
    return store.state._actorsTypes.sort()
  }

  setSelectedActorType (actor: string): void {
    store.state.setSelectedTypes(actor)
  }

  vuetifyFilterOveride (item: MauritanianActor | InternationalActor, queryText: string, itemText: string): boolean {
    return (item.nom.toLowerCase().includes(queryText.toLowerCase()) || item.sigle.toLowerCase().includes(queryText.toLowerCase()))
  }

  setChipStyle (actor: string) {
    if (store.state._filters.selectedTypes.includes(actor)) {
      return {
        border: '3px solid black'
      }
    }
    return {
      border: 'none'
    }
  }
}
